import React from "react";
import Header from "./Header";
import lessonIcon from '../img/uiItem.svg'
import stopWatch from '../img/stopwatch.svg'
import checkbox from '../img/checkbox.svg'
import colorWine from '../img/color__wine.jpg'
import maket from '../img/maket.jpg'
import maket2 from '../img/maket2.jpg'
import img2 from '../img/img2.jpg'
import motion from '../img/motion.gif'
import shadow from '../img/shadow.png'
import obraz from '../img/obraz.jpg'
import icon from '../img/icon.png'
import { Link } from "react-router-dom";

class Theory extends React.Component {
    render() {
        return (
            <div>
                <Header />
                <div className="theory__wrapper">
                    <h1>UI дизайн. Элементы</h1>
                    <div className="theory__img__wrapper">
                        <img className="theory__img" src={lessonIcon} alt='' />
                    </div>
                    <Link to={'../quiz'} className="btn__lesson btn__lesson--orange btn">Начать урок</Link>
                    <div className="theory__lesson">
                        <div className="theory__lesson--block">
                            <img className="" src={checkbox} alt='' />
                            <p>9 заданий</p>
                        </div>
                        <div className="theory__lesson--block">
                            <img className="" src={stopWatch} alt='' />
                            <p>4 мин</p>
                        </div>
                    </div>
                </div>
                <div className="theory__content">
                    <div>
                        <p className="theory__text">Дизайн пользовательского интерфейса — это внешний вид продукта. Основная цель дизайна пользовательского интерфейса — создать интуитивно понятный интерфейс для пользователей с помощью типографики, цвета, компонентов пользовательского интерфейса, микротекста, изображений и других элементов дизайна пользовательского интерфейса.</p>
                        <p className="theory__text">Продукт с хорошим UX подобен зданию с прочным фундаментом и удачным расположением, но без стен, оконных панелей, дверей, мебели и других элементов дизайна интерьера. Таким образом, дизайн пользовательского интерфейса использует свои визуальные и интерактивные ресурсы в тандеме с данными и контентом исследований дизайна UX, чтобы создать последовательный, интуитивно понятный и эстетически приятный интерфейс продукта.</p>
                    </div>
                    <div>
                        <img src={colorWine} alt="" />
                        <h2 className="theory__title">Цвет</h2>
                        <p className="theory__text">Цвет — это мощный инструмент, который люди используют для общения, стимулирования действий, влияния на настроение и даже изменения поведения. Это может повлиять на нашу производительность и потребительское поведение, повысить аппетит, вызвать беспокойство или даже успокоить эмоции. Однако, хотя некоторые цвета вызывают универсальные ассоциации, люди могут реагировать по-разному в зависимости от своих воспоминаний, культуры и текущего окружения.</p>
                        <p className="theory__text">Новые дизайнеры могут ошибочно полагать, что цвет — это просто декоративный инструмент для придания экрану красивого вида. На самом деле его цели — донести сообщение, сформировать поведение, повлиять на эмоции пользователей и привлечь внимание пользователей к важным вещам. Фактически, цвет является одним из наиболее важных элементов брендинга и демонстрирует индивидуальность продукта.</p>
                        <ul className="theory__list">
                            <li>Понимание <span className="theory__text--orange">теории цвета</span> — это крепкий кирпичик для построения набора навыков любого UX-дизайнера. Он предоставляет вам набор рекомендаций, на которые полагаются дизайнеры, чтобы передать сообщение или вызвать эмоции с помощью цвета в пользовательских интерфейсах.</li>
                            <li>Знание использования цветового круга, цветовых моделей, цветовых схем, оптических способностей человека, психологии и культуры является прочной основой для создания лучших цветовых палитр, которые работают.</li>
                        </ul>
                    </div>
                    <div>
                        <img src={maket} alt="" />
                        <h2 className="theory__title">Позитивное и негативное пространство</h2>
                        <p className="theory__text">Разница между позитивным и негативным пространством в дизайне не имеет ничего общего с системой координат. Позитивное пространство представляет собой область интереса. Проще говоря, пространство, занимаемое элементами дизайна, называется положительным.
                        </p>
                        <p className="theory__text">С другой стороны, негативное пространство (или белое пространство) — это пустая область между элементами дизайна, вокруг них и даже внутри них. Почему мы должны заботиться об этом? Пустое пространство — это передышка для элементов и групп контента, что значительно улучшает читаемость и упрощает просмотр жизненно важных элементов.</p>
                        <p className="theory__text">Свободное пространство делает дизайн аккуратным, удобным и свободным от беспорядка.[1] Добавление пространства вокруг элемента увеличивает его обнаруживаемость, направляет взгляд пользователей и выделяет его на фоне другого, менее важного контента.</p>
                        <p className="theory__text">Графические дизайнеры активно используют пустое пространство в логотипах, иллюстрациях, плакатах и ​​креативных надписях для обозначения ключевых объектов и создания уникальных композиций.</p>
                        <p className="theory__text">Дизайнеры также должны различать макро- и микропространство. Микропространство — это небольшое пространство между строками, абзацами или сеточными изображениями. С другой стороны, макропространство — это большая область вокруг макета дизайна и между его основными элементами[2]</p>
                    </div>
                    <div>
                        <img src={maket} alt="" />
                        <h2 className="theory__title">Типографика</h2>
                        <p className="theory__text">Типографику можно рассматривать с двух сторон. Во-первых, это стиль и внешний вид печатного текста. Во-вторых, это искусство и процесс упорядочивания текста, чтобы сделать его читабельным и разборчивым для удовлетворения потребностей в печатном или цифровом дизайне.
                        </p>
                        <p className="theory__text">Этот термин также обозначает такие действия, как выбор шрифтов, размеров точек, длины строк, межстрочного и межбуквенного интервалов, а также настройка расстояния между определенными парами букв (кернинг).[3]</p>
                        <p className="theory__text">Почему типографика так важна? Прежде всего, даже продукты, которые специализируются на визуальном контенте и не содержат длинных описаний или статей, имеют хоть какой-то письменный текст. Хорошая типографика устанавливает сильную визуальную иерархию и помогает пользователям ориентироваться в контенте и выделять более важные материалы.</p>
                        <p className="theory__text">Кроме того, правильный выбор и расположение шрифтов влияет на разборчивость и читабельность текста, делая ваш продукт более доступным и удобным для пользователя. Интересно, что высокая разборчивость не означает хорошую читаемость. Например, хорошо разборчивый шрифт может иметь низкую читабельность из-за небольшого размера, низкой контрастности или измененного отступа и интерлиньяжа. Дизайнеры должны выбрать шрифт с подходящей читабельностью в зависимости от назначения текста.</p>
                        <p className="theory__text">Наконец, типографика помогает повысить узнаваемость бренда. Уникальная последовательная типографика усиливает индивидуальность продукта и вызывает доверие у пользователей, поскольку пользователи узнают шрифт и ассоциируют его с брендом.</p>
                    </div>
                    <div>
                        <img src={img2} alt="" />
                        <h2 className="theory__title">Микроконтент</h2>
                        <p className="theory__text">Микроконтент — это тип UX-копирайтинга, который обычно передает сообщение через короткую фразу или текстовый фрагмент. Его основная цель — представить статью или сообщение в блоге, провести пользователей через интерфейс или побудить к действию. Примеры микроконтента включают заголовки страниц, заголовки страниц (заголовки), слоганы, тему электронной почты.
                        </p>
                    </div>
                    <div>
                        <img src={motion} alt="" />
                        <h2 className="theory__title">Motion</h2>
                        <p className="theory__text">Под «движением» мы подразумеваем анимацию или другие движущиеся визуальные эффекты, которые дизайнеры добавляют в интерфейсы по разным причинам. Хотя многие считают, что основная цель движения — сделать пользовательский опыт более приятным и интересным, это гораздо больше.
                        </p>
                        <p className="theory__text">Движение обеспечивает обратную связь для микровзаимодействий
                            Хорошая система информирует пользователей об изменениях статуса и предоставляет обратную связь об их действиях. Движение — стандартный инструмент для этого, так как оно увеличивает шансы пользователей заметить обратную связь. Например, анимированный счетчик указывает на то, что страница загружается, или появляется небольшая анимация с успешным сообщением после отправки электронного письма.
                        </p>
                        <p className="theory__text">Движение привлекает внимание пользователя
                            Эволюционно наши глаза притягиваются к движущимся объектам. Благодаря этому защитному механизму люди могут распознать опасность и защитить себя, пока не стало слишком поздно. В интерфейсах анимация также помогает привлечь внимание, но может сильно отвлекать, если использовать ее без ограничений.
                        </p>
                        <p className="theory__text">Движение направляет пользователей по странице
                            Анимация может помочь пользователям определить свое местоположение на странице и предотвратить дезориентацию, особенно на мобильных устройствах. Когда пользователи используют якорные ссылки или аккордеоны для просмотра дополнительного контента, плавная анимация показывает, где они сейчас находятся и как они туда попали.
                        </p>
                        <p className="theory__text">Движение указывает на взаимодействие
                            Анимация может служить указателем действия. Например, короткая анимация показывает, что вы можете удалить или заархивировать чат, проведя по нему пальцем и выбрав нужный вариант.[6]
                        </p>
                    </div>
                    <div>
                        <img src={shadow} alt="" />
                        <h2 className="theory__title">Тени</h2>
                        <p className="theory__text">В физическом мире тени отбрасываются всякий раз, когда объект препятствует источнику света. В цифровых интерфейсах дизайнеры также используют тени для придания элементам естественной эстетики. Что еще более важно, тени добавляют глубину и выделяют элементы на фоне.
                        </p>
                        <p className="theory__text">Внешние тени или тени — распространенный визуальный эффект в пользовательском интерфейсе. Они используют смещение по осям x, y или по обеим осям и имеют различные эффекты размытия и значения непрозрачности.
                        </p>
                        <p className="theory__text">Исследование пользователей, проведенное Google Material Design, показало, что тени или контуры штрихов помогают пользователям определить, кликабельны ли элементы быстрее или нет. Тени также увеличивают возможность сканирования, поэтому пользователи могут легче и быстрее обнаруживать элементы на странице.[7]
                            При чрезмерном или неправильном использовании тени могут внести визуальную путаницу в дизайн и повредить его удобству использования.
                        </p>
                    </div>
                    <div>
                        <img src={obraz} alt="" />
                        <h2 className="theory__title">Образы</h2>
                        <p className="theory__text">В физическом мире тени отбрасываются всякий раз, когда объект препятствует источнику света. В цифровых интерфейсах дизайнеры также используют тени для придания элементам естественной эстетики. Что еще более важно, тени добавляют глубину и выделяют элементы на фоне.
                        </p>
                        <p className="theory__text">Внешние тени или тени — распространенный визуальный эффект в пользовательском интерфейсе. Они используют смещение по осям x, y или по обеим осям и имеют различные эффекты размытия и значения непрозрачности.
                        </p>
                        <p className="theory__text">Исследование пользователей, проведенное Google Material Design, показало, что тени или контуры штрихов помогают пользователям определить, кликабельны ли элементы быстрее или нет. Тени также увеличивают возможность сканирования, поэтому пользователи могут легче и быстрее обнаруживать элементы на странице.[7]
                            При чрезмерном или неправильном использовании тени могут внести визуальную путаницу в дизайн и повредить его удобству использования.
                        </p>
                    </div>
                    <div>
                        <img src={icon} alt="" />
                        <h2 className="theory__title">Иконки</h2>
                        <p className="theory__text">Макет — это фрейм, который содержит визуальные элементы (изображения, текст, компоненты пользовательского интерфейса) в сетке для передачи определенного сообщения. Сетки — это невидимые пересекающиеся вертикальные и горизонтальные линии, которые делят страницу на разделы.
                        </p>
                        <p className="theory__text">Структура макета зависит от целей вашего проекта и основывается на организационных принципах композиции для создания профессиональных результатов. Дизайнер может экспериментировать с принципами композиции и соответствующим образом размещать элементы, чтобы направлять взгляд пользователей на интересующие их объекты.[1]
                        </p>
                        <p className="theory__text">Какие принципы влияют на структуру макета? Среди прочего:
                        </p>
                        <ul className="theory__list">
                            <li>Форма и пропорция,</li>
                            <li>Остаток средств,</li>
                            <li>Негативное пространство,</li>
                            <li>Цвет,</li>
                            <li>Контраст,</li>
                            <li>Ритм.</li>
                        </ul>
                        <p className="theory__text">Создание макетов с сеткой помогает организовать контент и сделать его более сбалансированным. Независимо от целей вашего проекта, сетки помогают сделать любой макет математически привлекательным и гармоничным. Сетки столбцов, вероятно, являются наиболее распространенным и гибким решением для организации контента.</p>
                        <p className="theory__text">Можете ли вы выйти из сети при создании макета? Абсолютно. Когда все сделано правильно, это увеличивает контраст и дает хорошую возможность для создания уникальных, художественных веб-сайтов. Однако то, что может работать для целевых страниц или веб-сайтов с портфолио фотографий, принесет больше вреда, чем пользы для целевых продуктов.</p>
                    </div>
                    <div>
                        <img src={maket2} alt="" />
                        <h2 className="theory__title">Макет</h2>
                        <p className="theory__text">Значки – это пиктограммы, широко используемые в веб-интерфейсах и мобильных устройствах. Они визуально представляют идеи, объекты или действия и помогают пользователям понять, как они могут использовать элемент (например, кнопку или пункт меню).[1]
                        </p>
                        <p className="theory__text">При правильном дизайне они помогают пользователям выполнять задачи. Наиболее распространенные рекомендации по применению значков включают в себя:
                        </p>
                        <p className="theory__text">Придерживайтесь универсальных иконок
                            Использование уникальных иконок увеличивает когнитивную нагрузку и мешает пользователям быстро и легко перемещаться по веб-сайтам. При разработке набора иконок подумайте, есть ли знакомая метафора для этой концепции, и не изобретайте велосипед. Например, увеличительное стекло — известный символ поиска.</p>
                        <p className="theory__text">Используйте последовательный дизайн
                            Если вы используете значки с заливкой на одной странице, вы не должны использовать значки с контуром где-либо в том же интерфейсе. Если вы придерживаетесь закругленных краев, убедитесь, что вы сохраняете этот стиль везде. В противном случае вы создадите визуальный беспорядок и дополнительные точки трения.</p>
                        <p className="theory__text">Не переусердствуйте со стилем значков
                            Использование слишком большого количества линий и украшений затрудняет интерпретацию значков для большинства пользователей, особенно для людей с нарушениями зрения. Иконки должны быть простыми и функциональными, а не сложными.
                        </p>
                    </div>

                    <Link to={'../quiz'} className="btn__lesson btn__lesson--orange btn btn__lesson--width">Начать урок</Link>
                </div>

            </div>
        )
    }
}


export default Theory